import React from 'react';
import SliderButton from '../buttons/slider-button.component'
import './project-card.styles.scss';

export const ProjectCard = props => (
  <div
    className='project-card'>
    <div className='project-contents'>
      <h1>{props.card.title}</h1>
      <p>{props.card.text}</p>
      <ul>
        {props.card.topics.map((topic, i) => (<li key={i}> {topic} </li> ))}
      </ul>
      <div className='buttons-lc'>    
      <a href='/' target='_blank'><SliderButton>View Code</SliderButton></a>
      <a href='/' target='_blank'><SliderButton>Live Demo</SliderButton></a>

      </div>
    </div>
  </div>
);
